.body {
  background: url("./imgs/background.jpg") no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  font-family: "Open Sans", sans-serif !important;
}

.navbar {
  font-family: "Open Sans", sans-serif !important;
  color: rgb(180, 119, 87) !important;
  background-color: rgba(228, 228, 228, 0.85);
}

.navbar-brand {
  font-family: "Open Sans", sans-serif !important;
  color: #3485a0 !important;
  font-weight: bolder;
}

.nav-link {
  color: rgb(52, 133, 160) !important;
}

.nav-link:active {
  color: rgba(24, 63, 77, 0.719) !important;
}

.nav-link:hover {
  color: rgba(24, 63, 77, 0.719) !important;
}

.card {
  background-color: rgba(228, 228, 228, 0.85);
  color: #183f4d;
}

.text-responsive {
  font-size: calc(50% + 0.5vw + 0.5vh);
}

.card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  object-position: top;
  align-content: center;
  min-width: 200px;
}

.card-img-top {
  max-height: 200px;
}

.contactMe {
  min-width: 200px;
}

.profilepic img {
  min-width: 200px;
  min-height: 200px;
}
